import React, {useEffect, useState, useRef} from 'react';

// material-ui
import { makeStyles } from '@material-ui/styles';

// project imports
import Header from './Header';
/* import Feature from './Feature';
import Demos from './Demos';
import Layouts from './Layouts'; */
import KeyFeature from './KeyFeature';
/* import Subscribe from './Subscribe'; */
import Footer from './Footer';

// project imports additions
import Products from '../../application/e-commerce/Products'
import Stores from '../../application/e-commerce/Stores'
import FloatingCart from 'ui-component/cards/FloatingCart';
import FloatingArrow from 'ui-component/cards/FloatingArrow';
import CheckoutModal from '../../application/e-commerce/Checkout/CheckoutModal'
import useAuth from 'hooks/useAuth'

import MenuIcon from '@material-ui/icons/Menu';
import KeyboardDoubleArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import {Button, IconButton, Typography} from '@material-ui/core';

import Slider from 'react-slick';


import Customization from 'layout/Customization';
import AppBar from 'ui-component/extended/AppBar';



// style constant
const useStyles = makeStyles((theme) => ({
    header: {
        paddingTop: '30px',
        marginLeft: '10px',
        marginRight: '10px',
        overflowX: 'hidden',
        overflowY: 'clip',
        [theme.breakpoints.down('sm')]: {
            paddingTop: '0px'
        }
    },
    sectionWhite: {
        paddingTop: '160px',   
        [theme.breakpoints.down('sm')]: {
            paddingTop: '60px',
            paddingRight: '10px'
        }
    }
}));

// =============================|| LANDING MAIN ||============================= //

const Landing = () => {
    const classes = useStyles();
    const {isCustomerLoggedIn} = useAuth()

    const myRef = useRef(null)

    const executeScroll = () => myRef.current.scrollIntoView({behavior: "smooth"})

    return (
        <>
            <div id="home" className={classes.header}>
                <AppBar />
                <Header /> 
                
            </div>
            <div>
                {/* <Typography
                                    variant="h4"
                                    component="div"
                                    color="inherit"
                                    sx={{
                                        fontSize: { xs: '1rem', md: '1.125rem' },
                                        fontWeight: '400',
                                        lineHeight: { xs: '24px', md: '20px' },
                                        marginTop: '150px',
                                        marginLeft: '400px',
                                        
                                    }}
                                >
                                    Make an impact with each transaction
                                </Typography> */}
                <IconButton onClick={executeScroll} style={{borderRadius: '50%'}} sx={{ '&:hover': { bgcolor: '#f68e1f' }, mt: '65px',background: '#3f7e44', left: '90%', display: {xs:'none', sm: 'none', md: 'block'} }} size="large">
                    <KeyboardDoubleArrowDownIcon sx={{ color: '#fff', fontSize: '40px' }} />
                </IconButton>
                </div>
            {/* <div className={classes.sectionWhite}>
                <Feature />
            </div>
            <div className={classes.sectionWhite}>
                <Demos />
            </div>
            <div className={classes.sectionWhite}>
                <Layouts />
            </div> */}
            {/* <div className={classes.sectionWhite} style={{paddingLeft: '60px', paddingRight: '60px'}}>
                <Products />
            </div> */}
            <div ref={myRef} className={classes.sectionWhite} style={{paddingLeft: '40px', paddingRight: '40px'}}>
                <Stores />
            </div>
            {/* <div className={classes.sectionWhite} style={{paddingLeft: '60px', paddingRight: '60px'}}>
                <CheckoutModal />
            </div> */}
            {/* <div className={classes.sectionWhite}>
                <KeyFeature />
            </div> */}
            {/* <div className={classes.sectionWhite}>
                <Subscribe />
            </div> */}
            <Footer />
            {isCustomerLoggedIn && <FloatingCart />}
            {/* <FloatingArrow/> */}
            {/* <Customization /> */}
        </>
    );
};

export default Landing;
