import React, {useState} from 'react';
import { sum } from 'lodash';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';

//Additions

import useAuth from 'hooks/useAuth';

// material-ui
import { useTheme, styled } from '@material-ui/styles';
import { Fab, Badge, IconButton } from '@material-ui/core';

// assets
import ArrowDownwardTwoToneIcon from '@material-ui/icons/ArrowDownwardTwoTone';

const StyledBadge = styled(Badge)(({ theme }) => ({
    '& .MuiBadge-badge': {
        right: 0,
        top: 3,
        border: `2px solid ${theme.palette.background.paper}`,
        padding: '0 4px'
    }
}));

// ===========================|| CART ITEMS - FLOATING BUTTON ||=========================== //

const FloatingArrow = () => {

    const { isCustomerLoggedIn } = useAuth();
    const [open, setOpen] = React.useState(false);
    const navigate = useNavigate();

    const theme = useTheme();

    const scroll = () => {
        console.log("Down we go")
        window.scrollTo({
            top: '1000px',
            behavior: "smooth",
        })
        console.log("Down we go")
    }


    return (
        <>
        <Fab
            /* component={Link}
            to="/heckout" */
            size="large"
            variant="string"
            sx={{
                top: '70%',
                position: 'fixed',
                right: 0,
                zIndex: (theme) => theme.zIndex.speedDial,
                boxShadow: theme.customShadows.warning,
                bgcolor: 'success.main',
                color: 'success.light',
                // borderRadius: '50%',
                '&:hover': {
                    bgcolor: 'success.dark',
                    color: 'success.light'
                }
            }}
            /* onClick={cartIconClicked} */
            
        >
            <IconButton disableRipple  sx={{ '&:hover': { bgcolor: 'transparent' } }} size="medium">
                    <ArrowDownwardTwoToneIcon sx={{ color: 'grey.800' }} />
            </IconButton>
        </Fab>
        </>
    );
};

export default FloatingArrow;
